@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400:700&display=swap');

html[lang="ja"] {
  font-family: "Noto Sans JP", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.underline {
  border-bottom: 2px solid #00C0AB;
}

@media (max-width:768px) {
  .header_title {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: bold;
    font-size:32px;
    margin-top:48px;
  }

  .header_caption {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-size:12px;
    margin-top:16px;
  }
}



@media (min-width:769px) {
  .header_title {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: bold;
    font-size:64px;
    margin-top:48px;
  }

  .header_caption {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-size:16px;
    margin-top:16px;
  }
}

body {
  background-color:#001D1A;
  margin-bottom:100px;
}


.mode_switch {
  transition: opacity 0.2s;
  cursor: pointer;
  cursor: hand;
}

.mode_switch:hover {
  opacity: 0.6; 
}

.genre_intro {
  width:120px;
  border-radius:12px;
  background-color:rgba(255,255,255,0.25);
  border-width:0;
  margin-right:8px;
  transition: opacity 0.2s;
  height:36px;
  color:white;
  font-weight:bold;
  font-size:15px;
}

.genre_intro:hover {
  background-color:rgba(255,255,255,0.25);
  opacity:0.6
}

.genre_zoom {
  width:120px;
  border-radius:12px;
  background-color:rgba(0,192,171,0.5);
  border-width:0px;
  transition: opacity 0.2s;
  height:36px;
  color:white;
  font-weight:bold;
  font-size:15px;
}

.genre_zoom:hover {
  background-color:rgba(0,192,171,0.5);
  opacity:0.6
}

hr {
  margin:5px;
  margin-left:0px;
  margin-right:0px;
}